.App {
  text-align: center;
  background-color: #75485E;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.App-header {
  min-width: 100%;
  background-color: #137547;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: 'Gorditas', cursive;
  font-size: calc(20px + 3vmin);
  color: #CEFF1A;
}

button {
  color: #137547;
  background-color: #CEFF1A;
  padding: 10px;
  margin: 10px;
  border-radius: 20px;
  border: none;
  text-transform: uppercase;
  font-weight: bold;
}
